<template>
	 <div class="company">
	 	<div class="box">
    	<div class="content-box">
    		<h4>提现</h4>
    		<div class="content">
    			<div class="jin">
					<el-steps :space="300" :active="active" finish-status="success">
					  <el-step title="填写银行卡/金额"></el-step>
					  <el-step title="确认提现信息"></el-step>
					  <el-step title="申请提现成功"></el-step>
					</el-steps>
    			</div>
    			<div class="tet-two">
    				<i class="glyphicon glyphicon-exclamation-sign"></i>
    				可提现金额25.64元，充值金额0.00元，充值金额不允许提现，如需返回，请点击"<a href="#">发起充值退回</a>".
    			</div>
    			<div class="sele">
    				<ul>
    					
    						<li>
	    						<p>选择银行卡：</p>
	    						<router-link to="/two"><div class="yin"><i class="glyphicon glyphicon-plus"></i>添加银行卡</div></router-link>
	    					</li>
    					
    					
    					<li>
    						<p>提现金额：</p>
    						<el-input v-model="input"></el-input>元
    					</li>
    				</ul>
    				<div class="tet">
    					提现金额每次最高2万元，每日限3次，每日累计最高2万元，每月累计最高6万元<br />安装数字证书后，交易更安全且提升限额，建议您<a href="#">立即安装数字证书</a>
    				</div>
    			</div>
    		</div>
    		<el-button type="danger" style="margin-top: 12px; margin-left: 182px;" @click="next">下一步</el-button>
    	</div>
    	</div>
    </div>
</template>

<script>
import com from '@/dayun/child/tx'
  export default {
    data() {
      return {
        active: 0,
        input: ''
      }
    },
	components: {
		com
	},
    methods: {
      next() {
        if (this.active++ > 2) this.active = 0;
      }
    }
  }
</script>

<style lang="less" scoped>
	.company{
		.box{
			width:1600px;
			background: #fafbff;
			float: left;
			padding: 28px;
			margin-left: 2px;
			color: #bbbbbb;
		.content-box{
			border:1px solid #e8e8f4;
			background: #fff;
			padding-bottom: 50px;
			height:921px;
			h4{
				font-weight: bold;
				padding: 16px 0px 12px 20px;
				margin: 0 ;
				border-bottom: 1px solid #d0d0d0;
			}
			.content{
				.jin{
					width: 495px;
					margin: 18px 0px 12px 182px;
				}
				.tet-two{
					width: 1000px;
					margin:45px 0px 42px 182px ;
					font-size: 12px;
					background: #ffff8d;
					color: #ff8b03;
					padding: 8px ;
				}
				.sele{
					margin-left: 95px;
					ul{
						li{
							height: 50px;
							display: flex;
							align-items: center;
							text-align: center;
							color: #bbbbbb;
							p{
								width: 100px;
								display: flex;
								justify-content: flex-end;
								margin: 0;
							}
							.yin{
								width: 200px;
								height: 40px;
								border: 3px dashed #e6e6e6;
								border-radius: 10px;
								display: flex;
								justify-content: center;
								align-items: center;
								color: #e6e6e6;
							}
							.el-input{
								width: 200px;
							}
						}
						
					}
					.el-button{
							background: red;
								/*background: #f1414c;
								border: 1px solid #c81c24;*/
							}
					.tet{
							margin-left: 105px;
							font-size: 12px;
							color: #bbbbbb;
							}
				}
			}
		}
		}
	}
</style>


